@use "~@infineon/design-system-tokens/dist/tokens";
@use "../../global/font.scss";

:host { 
  display: inline-flex;
}

.link {
  display: inline-flex;
  align-items: center;
  font-weight: 400;
  font-size: tokens.$ifxFontSizeM;
  text-decoration: none;
  color: tokens.$ifxColorOcean500;
  gap: tokens.$ifxSpace100;
  line-height: 1.6;

  &:focus { 
    outline-width: 2px;
    outline-style: solid;
    outline-color: #0A8276;
    outline-offset: 2px;
    border-radius: 3px;
  }

  &.bold { 
    font-weight: tokens.$ifxFontWeightSemibold;

    &:hover { 
      text-decoration: underline;
    }

    &:active { 
      color: #08665C;
      font-style: normal;
      font-weight: 600;
      text-decoration: underline;
    }
  }

  &.underlined { 
    text-decoration: underline;
    text-decoration-color: tokens.$ifxColorOcean500;
    color: #1D1D1D;

    &:hover { 
      color: tokens.$ifxColorOcean500;;
    }

    &:active {
      color: #0A8276;
    }
  }

  &.title { 
    text-decoration: none;
    font-weight: tokens.$ifxFontWeightSemibold;
    color: #1D1D1D;
    
    &:hover { 
      color: tokens.$ifxColorOcean500;
    }

    &:active { 
      color: #08665C;
    }
  }

  &.menu { 
    font-weight: 400;
    font-style: normal;
    line-height: 26px; 
    color: #1D1D1D;

    &:hover { 
      color: tokens.$ifxColorOcean500;
    }

    &:active { 
      color: #08665C;
    }
  }



  &.small { 
    font-size: tokens.$ifxFontSizeS;
  }

  &.medium { 
    font-size: tokens.$ifxFontSizeM;
  }

  &.large { 
    font-size: tokens.$ifxFontSizeL;
  }

  &.extraLarge { 
    font-size: tokens.$ifxFontSizeXl;
  }

  // &.bold {
  //   font-weight: tokens.$ifxFontWeightSemibold;
  // }

  // &.primary {
  //   color: tokens.$ifxColorOcean500;
  // }

  // &.secondary {
  //   color: tokens.$ifxColorBerry500;
  // }

  // &.warning {
  //   color: tokens.$ifxColorOrange500;
  // }

  // &.danger {
  //   color: tokens.$ifxColorRed500;
  // }

  // &.success {
  //   color: tokens.$ifxColorGreen500;
  // }

  &.underline {
    text-decoration: underline;
  }
}